            height:'',
            weight:'',
            bloodType:'',
            photos:[],
            documents:[]

          }
      },
      getFile(el){
          console.log(this.$refs.file.files[0]);
          this.src=this.$refs.file.files[0].type+'/'+this.$refs.file.files[0].name;
          console.log(el.target.result);

      },
      tab(){
          this.$events.emit('tab','');
      },
      saveAll(){
          let vm=this;
          if(vm.roomInfo.building=="" || vm.roomInfo.roomNumber==""){
              alert("");
              return;
          }
          axios.post('/roomRecorded',roomInfo)
          .then(function(){

          })
          .catch(function(err){
              console.log(err);
          })
          

      },
      cancelRenter(){
          this.tenantDisp=false;
          this.initRenter();
      },
      saveRenter(){
          let vm=this;
          if(vm.renterInfo.ownerName == "" || vm.renterInfo.ownerName == null ||
           vm.renterInfo.personalid == "" || vm.renterInfo.personalid == null){
               alert("");
              return;
          }
          vm.renterArr.push(vm.renterInfo);
          vm.initRenter();
      },
      delRenter(){//

      }

  },
  beforeDestroy(){
       
  },
  components:{
    
  }
  
}
</script>
<style scoped>
#recorded{
    top: 0;    
    left:200px;
    width:calc(100% - 200px);
    background-color:rgba(0,0,0,.3);
}
.recordLabel{
    width:30%;
    display:inline-block;
    font-size:14px;
}
.recordForm{
    width:60%;
    appearance:none;
    border:0;
    border-bottom:1px solid #BDBDBD;
    display:inline-block;
    outline:none;
}
</style>




